.main-nav--opened .main-nav__burger-line {
  position: absolute;
  width: 30px;
  height: 3px;
  left: 50%;
  top: 10px;
  transform: translate(-50%);
  background-color: var(--light-color);
}
.main-nav--opened .main-nav__burger-line:first-child {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__burger-line:first-child:focus {
  background-color: var(--red-color);
}
.main-nav--opened .main-nav__burger-line:nth-child(2) {
  display: none;
}
.main-nav--opened .main-nav__burger-line:last-child {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__burger-line:last-child:focus {
  background-color: var(--red-color);
}

