  .effect__wrapper {
    max-width: 980px;
    grid-template-columns: 370px 1fr;
  }
  .effect__wrapper::before {
    width: 298px;
    height: 345px;
    background-image: url("../img/timer-desc.png");
  }
}
@media (min-width: 1080px) and (min-resolution: 2dppx) {
  .effect__wrapper::before {
    background-image: url("../img/timer-desc@2x.png");
